import React, { useState } from "react";
import { Link , NavLink } from "react-router-dom";
import logo from "../../imgs/logo.png";

const Header = () => {
  const [toggle, setToggle] = useState(false);
  const Humberger = () => {
    return (    
      <div id={toggle ? "open-humberger" : ""} className= 'navigation__humberger' onClick={() => setToggle(!toggle)}>
        <span style={{transform: toggle ? 'rotate(45deg)' : 'rotate(0)'}}></span>
        <span style={{backgroundColor: toggle ? 'transparent' : '#242f51'}}></span>
        <span style={{transform: toggle ? 'rotate(-45deg)' : 'rotate(0)'}}></span>
      </div>

    );
  }
  const NavList = () => (
    <>
      <ul className={`navigation__list ${toggle && "open"}`}>
        <li className="navigation-item"  onClick={() => setToggle(!toggle)}>
          <NavLink exact to="/" className="navigation__list--link ">
            home
          </NavLink>
        </li>
        <li className="navigation-item"  onClick={() => setToggle(!toggle)}>
        <a href="/#projects" className="navigation__list--link ">
            projects
          </a>
        </li>
        <li className="navigation-item"  onClick={() => setToggle(!toggle)}>
        <NavLink to="/contact" className="navigation__list--link ">
            contact
          </NavLink>
        </li>
      </ul>
    </>
  );
  return (
    <header className="header">
    <a href="/" >
      <div className="header__logo">

        <img src={logo} alt="logo" className="header__logo-logo" />
        <h3>ITGC</h3>
      </div></a>
      <nav className="navigation">
        <NavList />
      </nav>
      <Humberger />
    </header>
  );
};

export default Header;
