import React from 'react';
import HeroImage from '../../imgs/hero.svg';
import white from '../../imgs/whiteSpace.svg';


const Landing = () => {
    return (
        <section className="landing">
            <div className="landing__info">
                <h1 className="info-title"><span className="u-upper-case">it</span> guys community</h1>
                <p className="info-subtitle">get out from your cage and meet good friends in your college</p>
                <a className="info-cta btn" href="/">more info</a>
                
            </div>
            <div className="landing__img">
                <img src={HeroImage} alt="illustration"/>
            </div>
            <img src={white} alt="white" className="whiteSpace"/>
        </section>
    )
}

export default Landing
